import React, { useRef, useEffect, useState } from 'react'
import mapboxgl from 'mapbox-gl';

function Map() {
    mapboxgl.accessToken = 'pk.eyJ1IjoidG9jaGF1MjAxNiIsImEiOiJja29mMGY4bGgwZDhxMnBqeHhqYmQ0a2U1In0.SN35ZPdW9uALvZYIOWmGXQ';

    const mapContainer = useRef(null);
    const map = useRef(null);
    const [lng, setLng] = useState(108.32850184606477);
    const [lat, setLat] = useState(15.876816510258282);
    const [zoom, setZoom] = useState(18);
    useEffect(() => {
        if (map.current) return; // initialize map only once
        map.current = new mapboxgl.Map({
            container: mapContainer.current, //chỗ này là id của div
            style: 'mapbox://styles/mapbox/streets-v11',
            center: [lng, lat],
            zoom: zoom
        });
    });

    return (
        <div>
            <div ref={mapContainer} className="map-container" /> {/* id nằm ở đây */}
        </div>
    )
}

export default Map
